.App {
  text-align: center;
  height: 100vh;
  width: 400px;
  background-image: url('./assets/images/background.png');
  background-size: contain;
  min-height: 100vh;
  margin: 0 auto;
  max-width: 31.25em;
  min-width: 18.75em;
  background-color: white;
}

.App__logo-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  padding-top: 9rem;
}

.App__logo {
  height: 5rem;
}

.App__result {
  z-index: 1000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}

.App__tries-wrapper {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1rem;
  margin: 0 0 1rem 1rem;
}

.App__tries-img {
  height: 1rem;
  margin-right: 0.5rem;
}

.App__tries-text {
  color: yellow;
  margin: 0;
}



